.heatlhItemGrid {
  width: min(800px, 100vw);
  width: 80%;
  margin: 1vh auto;
  display: grid;
  grid-template-columns: 50% 30%;
  grid-template-rows: repeat(1, 2vh);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  color: rgb(235, 227, 139);
  background-color: rgb(79, 94, 125);
  padding: 1vh;
  box-shadow: rgba(19, 19, 20, 0.5) 5px 5px 7px 1px;
}

