.displayFrame {
  display: grid;
  grid-template-columns: repeat(3, 2fr) 8fr 2fr;
  grid-template-rows: repeat(1, 3vh);
  grid-column-gap: 6px;
  grid-row-gap: 6px;
  width: min(800px, 100vw);
  max-width: 100%;
  margin-top: 1.2vh;
  margin-bottom: .7vh;
}

.volumesMainGrid {

display: grid;
  grid-template-columns: repeat(3, 2fr) 8fr 2fr;
  grid-template-rows: repeat(1, 3vh);
  grid-column-gap: 6px;
  grid-row-gap: 6px;
  width: min(800px, 100vw);
  max-width: 100%;
margin-bottom: .4vh;
}

